<template>
  <div class="goods">
    <van-nav-bar title="员工管理" left-arrow :fixed="true" @click-left="onClickLeft">
      <!-- <template #right>
        <van-icon name="plus" size="18" />
      </template> -->
    </van-nav-bar>
    <div style="height: 12.26667vw;"></div>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div style="padding:0 10px;" v-for="(item,index ) in list" :key="index">
        <div
          style="padding:10px 0px;display: flex;align-items: center;justify-content: space-between;border-bottom: 1px dotted #e0e0e0;"
        >
          <div style="display: flex;align-items: center;flex:1;">
            <!-- <img
              style="width: 40px;height: 40px;border-radius: 20px;"
              src="https://img.yzcdn.cn/vant/tree.jpg"
            /> -->
             <div style="font-size: 13px;background:#8ea0f2;color:white;width: 40px;height: 40px;border-radius: 20px;line-height:40px;text-align:center;">{{index+1}}</div>
            <div style="margin-left: 20px;">
              <div style="color: #7e7e7e;font-size: 15px;">{{item.username}}</div>
              <div style="margin-top: 10px;color: #808080;font-size: 14px;">{{item.depart_name||"员工"}} | {{item.status==1?'开启':"未开启"}}</div>
            </div>
          </div>
          <div>
            <div style="color: #7a7a7a;font-size: 13px;heigth:15px;">{{item.mobile||"不详"}}</div>
            <div style="color: #7a7a7a;font-size: 13px;margin-top: 14px;">√正常 | 已绑定</div>
          </div>
        </div>
      </div>
      <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
    </van-list>

    <van-tabbar v-model="active" :border="true">
      <van-tabbar-item icon="gem-o" to="/">商机管理</van-tabbar-item>
      <van-tabbar-item icon="user-o" to="/user">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem, Toast, NavBar, List, Icon } from "vant";

export default {
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
    [NavBar.name]: NavBar,
    [List.name]: List,
    [Icon.name]: Icon,
  },

  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      active: 1,
    };
  },

  methods: {
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      let that = this;
      // eslint-disable-next-line no-console
      console.log("submit");
      this.$http
        .post("/admin/user/lists", {
          page_nums: 20,
          page: this.page,
          keywords: "",
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            //   // 数据全部加载完成
            that.list = that.list.concat(response.data.data.list);
            // eslint-disable-next-line no-console
            console.log(that.list);
            if (response.data.data.list.length < 20) {
              that.finished = true;
            }
          }
          that.loading = false;
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          that.loading = false;
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          // Toast("登录失败");
        });
    },
    formatPrice() {
      return "¥" + (this.goods.price / 100).toFixed(2);
    },
    onClickCart() {
      this.$router.push("cart");
    },
    sorry() {
      Toast("暂无后续逻辑~");
    },
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      Toast("按钮");
    },
  },
};
</script>

<style lang="less">
.goods {
  padding-bottom: 50px;
}
</style>
